.form {
  width: 95%;
  max-width: 25rem;
  margin: 2rem auto;
}
.error {
  color: red;
  text-align: start;
  font-size: 0.8rem;
  margin-top: 0;
}
.changed {
  color: green;
  text-align: start;
  font-size: 0.8rem;
  margin-top: 0;
}
.control {
  margin-bottom: 0.5rem;
}

.control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #353336;
  display: block;
}

.control input {
  display: block;
  font: inherit;
  width: 100%;
  border-radius: 4px;
  border: 1px solid #38015c;
  padding: 0.25rem;
  background-color: #f7f0fa;
}

.action {
  margin-top: 1.5rem;
}

.action button {
  font: inherit;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
  border-radius: 4px;
  background-color: #38015c;
  color: white;
  border: 1px solid #38015c;
}

.action button:hover {
  background-color: #540d83;
  border-color: #540d83;
}
